import React, { MutableRefObject, memo, useRef, useState } from 'react';
import {
    SCheckbox,
    SEditInput,
    STodoContainer,
    STodoDeleteButton,
    STodoEditButton,
    STodoLabel,
} from './styles/todo.styles';
type TodoType = {
    id: string;
    title: string;
    completed: boolean;
    deleteTodo: Function;
    editTodo: Function;
	style:any;
};

const Todo: React.FC<TodoType> = ({ id, title, completed, deleteTodo, editTodo, style}) => {
    const [isEdit, setIsEdit] = useState<boolean>(false);
    const inputRef = useRef() as MutableRefObject<HTMLInputElement>;

    function handleEditTodo() {
        if (isEdit) {
            setIsEdit(false);
            if (inputRef.current.value.trim().length > 0 && inputRef.current.value.trim() !== title) {
                editTodo({ id: id, title: inputRef.current.value.trim(), completed: completed });
            }
        } else {
            setIsEdit(true);
            queueMicrotask(() => {
                inputRef.current.value = title;
            });
        }
    }
    function handleDeleteButton() {
        if (isEdit) {
            setIsEdit(false);
        } else deleteTodo(id);
    }

    return (
        <STodoContainer style={style}>
            {isEdit ? (
                <SEditInput ref={inputRef} type='text' placeholder='..' />
            ) : (
                <>
                    <SCheckbox
                        type='checkbox'
                        id={id}
                        checked={completed}
                        onChange={() => editTodo({ id, title: title, completed: !completed })}
                    />
                    <STodoLabel htmlFor={id}>{title}</STodoLabel>
                </>
            )}
            <STodoEditButton onClick={handleEditTodo}>{isEdit ? '' : ''}</STodoEditButton>
            <STodoDeleteButton onClick={handleDeleteButton}>{isEdit ? '' : ''}</STodoDeleteButton>
        </STodoContainer>
    );
};

export default memo(Todo);
